// in src/users.js
import React from "react";
import {
  List,
  Datagrid,
  TextField,
  Filter,
  TextInput,
  ReferenceInput,
  SelectInput,
  SimpleList,
  Responsive,
  ReferenceField,
  Show,
  SimpleShowLayout,
  // DateField,
  ShowButton,
  TabbedShowLayout,
  Tab
  // RichTextField,
  // NumberField,
  // BooleanField,
  // ReferenceManyField,
  // EditButton
} from "react-admin";

const TranFilter = props => (
  <Filter {...props}>
    <TextInput label="Search" source="q" alwaysOn />
    <ReferenceInput
      label="Status"
      source="statusId"
      reference="stransaksi"
      allowEmpty
    >
      <SelectInput optionText="status" />
    </ReferenceInput>
  </Filter>
);

export const ChekTransaksi = props => (
  <List title="Check Transaction" {...props} filters={<TranFilter />}>
    <Responsive
      small={
        <SimpleList
          primaryText={record => record.jenissampah}
          secondaryText={record => record.jumlahsampah}
          tertiaryText={
            record => record.tanggal
            // new Date(record.published_at).toLocaleDateString()
          }
        />
      }
      medium={
        <Datagrid>
          <TextField source="id" />

          <ReferenceField
            label="Penjual"
            source="customerId"
            reference="customers"
          >
            <TextField label="Nama Penjual" source="nama" />
          </ReferenceField>

          <TextField label="Jenis Sampah" source="jenissampah" />
          <TextField label="Jumlah Sampah" source="jumlahsampah" />
          <TextField source="tanggal" />

          <ReferenceField
            label="Status Transaksi"
            source="statusId"
            reference="stransaksi"
          >
            <TextField source="status" />
          </ReferenceField>

          <ShowButton />
        </Datagrid>
      }
    />
  </List>
);

export const TranShow = props => (
  <Show {...props}>
    <SimpleShowLayout>
      <ReferenceField label="Penjual" source="customerId" reference="customers">
        <TextField source="nama" />
      </ReferenceField>

      <ReferenceField label="No.HP" source="customerId" reference="customers">
        <TextField source="hp" />
      </ReferenceField>

      <ReferenceField label="Alamat" source="customerId" reference="customers">
        <TextField source="alamat" />
      </ReferenceField>

      <TextField label="Jenis Sampah" source="jenissampah" />
      <TextField label="Harga Sampah" source="hargasampah" />
      <TextField label="Tanggal" source="tanggal" />

      <ReferenceField label="Status" source="statusId" reference="stransaksi">
        <TextField source="status" />
      </ReferenceField>

      <TabbedShowLayout>
        <Tab label="Detai Penjual">
          <ReferenceField
            label="Penjual"
            source="customerId"
            reference="customers"
          >
            <TextField source="nama" />
          </ReferenceField>
          <ReferenceField
            label="Alamat"
            source="customerId"
            reference="customers"
          >
            <TextField source="alamat" />
          </ReferenceField>

          <ReferenceField
            label="No.HandPhone"
            source="customerId"
            reference="customers"
          >
            <TextField source="hp" />
          </ReferenceField>

          <ReferenceField
            label="Email"
            source="customerId"
            reference="customers"
          >
            <TextField source="email" />
          </ReferenceField>
        </Tab>
        <Tab label="Detail Barang">
          <TextField label="Jenis Sampah" source="jenissampah" />
          <TextField label="Jumlah Sampah" source="jumlahsampah" />
          <TextField label="Tanggal" source="tanggal" />
        </Tab>
        <Tab label="Status Transaksi" path="miscellaneous">
          <ReferenceField
            label="Status Transaksi"
            source="statusId"
            reference="stransaksi"
          >
            <TextField source="status" />
          </ReferenceField>
        </Tab>
        <Tab label="Metode Pembayaran">
          <TextField source="metodepembayaran" />
        </Tab>
      </TabbedShowLayout>
    </SimpleShowLayout>
  </Show>
);
