import React from "react";
import Card from "@material-ui/core/Card";
import CardActions from "@material-ui/core/CardActions";
import CardText from "@material-ui/core/CardContent";
import CardHeader from "@material-ui/core/CardHeader";
import {
  List,
  TextField,
  //DateField,
  EditButton,
  ReferenceField
} from "react-admin";
import PersonAdd from "@material-ui/icons/PersonAdd";
import Avatar from "@material-ui/core/Avatar";

const cardStyle = {
  width: 300,
  minHeight: 300,
  margin: "0.5em",
  display: "inline-block",
  verticalAlign: "top"
};
const GAMBAR = ({ ids, data, basePath }) => (
  <div style={{ margin: "1em" }}>
    {ids.map(id => (
      <Card key={id} style={cardStyle}>
        <CardHeader
          title={<TextField record={data[id]} source="nama" />}
          //subtitle={<DateField record={data[id]} source="created_at" />}
          avatar={<Avatar icon={<PersonAdd />} />}
        />
        <CardText>
          <TextField record={data[id]} source="namabank" />
        </CardText>
        <CardText>
          about&nbsp;
          <ReferenceField
            label="MyAccount"
            resource="alamat"
            record={data[id]}
            // source="post_id"
            // reference="posts"
            basePath={basePath}
          >
            <TextField source="hp" />
          </ReferenceField>
        </CardText>
        <CardActions style={{ textAlign: "right" }}>
          <EditButton
            resource="myaccounts"
            basePath={basePath}
            record={data[id]}
          />
        </CardActions>
      </Card>
    ))}
  </div>
);

