// in src/trashpurchase.js
import React from "react";
import { List, Datagrid, TextField, ReferenceField } from "react-admin";

export const trashpurchase = props => (
  <List title="Trash Purchase List" {...props}>
    <Datagrid>
      <ReferenceField label="ID" source="customerId" reference="customers">
        <TextField source="id" />
      </ReferenceField>
    </Datagrid>
  </List>
);
